<template>
  <Row class="bi-tabs">
    <Column
      v-for="(item,index) in options"
      :key="index"
      x="c"
      :class="itemClass"
      :style="{'margin-right':options.length!==index+1?`${rowSpace}px`:`0px`}"
    >
      <Column x="c" y="e" class="position-r h-100b">
        <slot :name="item.key" :item="item" :isActive="item.key===modelValue">
          <el-button
            text
            plain
            :class="`${labelClass} ${item.key===modelValue?labelClassAc:labelColor}`"
            class="Bi-tab-button"
            @click.stop="tabClick(item)"
          >
            <Row :class="item.key===modelValue? 'bold' : 'font-w-400'">{{ item?.label }}</Row>
            <Row v-if="autoLine" class="bt-under-line w-100b" :class="`${lineClass} ${item.key===modelValue?'bg-primary':''}`" />
          </el-button>
        </slot>

        <Row v-if="!autoLine" class="bt-under-line" :class="`${lineClass} ${item.key===modelValue?'bg-primary':''}`" />
      </Column>
    </Column>
    <!-- <Column x="c">
      <el-button v-to="'/c2c/optional'" text>自选区</el-button>
      <Row class="w-20 h-4 bg-primary" />
    </Column> -->
  </Row>
</template>
<script setup>
const emit = defineEmits()
const props = defineProps({
  options: {
    require: true,
  },
  modelValue: {},
  labelColor: {
    default: 'c-info'
  },
  labelClass: {
    default: 'font-22'
  },
  autoLine: {
    default: false
  },
  lineClass: {
    default: 'w-24 h-4'
  },
  labelClassAc: {
    default: 'c-dark bold'
  },
  itemClass: {
    default: ''
  },
  rowSpace: {
    default: '40'
  }
})

// let labelClass=$computed(()=>{
//   return "font-22"
// })
// let lineClass=$computed(()=>{
//   return "w-24 h-4"
// })

const tabClick = (item) => {
  if (!props.value) {
  }
  emit('update:modelValue', item.key)
  emit('tabChange', item.key)
}

</script>
<style lang="scss">
@import "@/sass/var/index.scss";
.bi-tabs{
  --el-fill-color: transparent !important;
  --el-fill-color-light: transparent !important;
  .el-button:hover{
    background: rgba(255, 0, 0, 0) !important;
  }
}
.bt-under-line{
  position: absolute;
  bottom: 0px;
}
.Bi-tab-item{
  margin-right: 50px;
}
.Bi-tab-button{
  padding-left: 0px !important;
  padding-right: 0px !important;

}
</style>
